
.htmleaf-content {
	font-size: 150%;
	padding: 1em 0;
}
/*form styles*/
#msform {
	width: 1092px;
	margin: 50px auto;
	text-align: center;
	position: relative;
}
#msform fieldset {
	text-align: left;
	padding: 20px 30px;
	box-sizing: border-box;
	width: 80%;
	height: 20em;
	margin: 0 10%;
	
	/*stacking fieldsets above each other*/
	position: absolute;
}
#msform fieldset span{
	color:#939393;
	margin-left: 3em;
}
.detail{
	vertical-align: top;
}
/*Hide all except first fieldset*/
#msform fieldset:not(:first-of-type) {
	display: none;
}
/*headings*/
.fs-title {
	font-weight: bold;
	font-size: 1.2em;
	text-align: left;
	text-transform: uppercase;
	color: #696969;
	margin-bottom: 10px;
}
#msform h1{
	font-size: 3em;
	font-weight: bold;
	text-align: center;
	color: #52BAC7;
}

#msform p{
	display: inline-block;
}
hr{
	border-color:#AFB3BC;
}
/*inputs*/
input {
	height: 1em;
	padding: 15px;
	border: 1px solid #ccc;
	border-radius: 3px;
	margin-bottom: 1em;
	font-family: "Microsoft YaHei",montserrat;
	color: #2C3E50;
	font-size: 0.8em;
}
#msform textarea{
	height: 10em;
	width: 30em;
	vertical-align:top;
}
#btn-file{
	height: 2em;
	padding: 0;
	border: none;
	margin-left: 175px;
}
/*buttons*/
#msform .action-button {
	display: inline-block;
	width: 10em;
	height: 3em;
	background: #52BAC7;
	font-weight: bold;
	color: white;
	border: 0 none;
	border-radius: 1px;
	cursor: pointer;
	padding: 10px 5px;
}
#msform .action-button:hover, #msform .action-button:focus {
	box-shadow: 0 0 0 2px white, 0 0 0 3px #27AE60;
}
.previous{
	position: relative;
	margin-left: 58%;
	top: 4em;
}
.next{
	margin-left: 80%;
}
.btn-pay{
	display: inline-block;
	width: 10em;
	height: 3em;
	line-height: 3em;
	background: #52BAC7;
	font-weight: bold;
	color: white;
	cursor: pointer;
	margin-left: 8.5em;
	text-indent: 4em;
}
/*progressbar*/
#progressbar {
	width: 80%;
	margin-left: 10%;
	margin-bottom: 2em;
	overflow: hidden;
	/*CSS counters to number the steps*/
	counter-reset: step;
}
#progressbar li {
	list-style-type: none;
	text-transform: uppercase;
	font-size: 16px;
	width: 25%;
	float: left;
	position: relative;
}
#progressbar li:before {
	content: counter(step);
	counter-increment: step;
	width: 3em;
	line-height: 3em;
	display: block;
	font-size: 14px;
	color: #B0B4BD;
	background: #E1E4E9;
	border-radius: 50%;
	margin: 0 auto 5px auto;
}
/*progressbar connectors*/
#progressbar li:after {
	content: '';
	width: 100%;
	height: 10px;
	background: #E1E4E9;
	position: absolute;
	left: -50%;
	top: 1em;
	z-index: -1; /*put it behind the numbers*/
}
#progressbar li:first-child:after {
	/*connector not needed before the first step*/
	content: none; 
}
/*marking active/completed steps blue*/
/*The number of the step and the connector before it = grey*/
#progressbar li.active:before,  #progressbar li.active:after{
	background: #52BAC7;
	color: white;
}